  border-bottom: 2px solid #ffffff99;
  width: 20px;
  height: 30px;
  display: inline-block;
}
.reveal-letter {
color: white;
}
.hidden-letter {
  color: black;
}
.space-letter {
  width: 100%;
   border-bottom: 2px solid #00000000;
}
.letter-big {
  font-size: 48px;
  font-size: 22.4px;
  font-size: 6.2vh;
  line-height: 1.3;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  font-weight: light;
  text-transform: uppercase;
  margin: 0 .2vw;
  border-bottom: 2px solid rgba(255, 255, 255, 0.6);
  width: 3.8vw;
  height: 7.5vh;
  display: inline-block;
  border-radius: 3px;
  overflow: hidden;
  margin-top: 10px;
  transition: all 0.5s ease-in-out;
  animation: reveal 0.5s linear 1;
  animation-delay: 0s;
  transform-origin: center;
}
@keyframes reveal {
  0% {
    transform: rotateY(-180deg);
    color: #fff;

  }

  50%{
    transform: rotateY(-90deg);
    color: #fff;
  }
  100% {
    transform: rotateY(0deg);
    color: #210344;
  }
}
@keyframes reveal-again {
  0% {
    transform: rotateY(-180deg);
    color: #fff;

  }

  50%{
    transform: rotateY(-90deg);
    color: #fff;
  }
  100% {
    transform: rotateY(0deg);
    color: #210344;
  }
}

.reveal-letter-big  {
  background: white;
  transition: all 1s ease-in-out;
  animation: reveal-again 1s ease-in-out 0s 1; /*, glow 2s linear 2s infinite;*/
  color: #210344;
  transform-origin: center;
}
@keyframes glow {
    0% {
        box-shadow: 0 4px 15px transparent;
    }
    50% {
        box-shadow: 0 4px 15px #ffca36;
    }
    100% {
        box-shadow: 0 4px 15px transparent;
    }
}
.hidden-letter-big  {
  color: black;
  background: white;
}
.space-letter-big  {
  width: 5%;
  border: none;
  height: 0px!important;
}

